/*
    ** All elements border-box
*/
*,
*:before,
*::after {
  box-sizing: border-box;
}

/*
    ** Root element font size
    ** No default margins and paddings
*/
html,
body {
  font-size: 0.9rem;
  margin: 0;
  padding: 0;
}

/*
    **root elements display block
*/
html,
body {
  display: block;
}

/*
    ** Body main style
*/
body {
  line-height: 1.317101995;
  scroll-behavior: smooth;
  font-family: sans-serif;
  padding: 2rem 1rem;
  font-family: "Roboto", sans-serif;
}

/*Grid Sistem*/
.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  height: 100vh;
  max-width: 1024px;
}

.gs-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px;
  /*justify-content: center;*/
}

/*Agrgando un selector de estilos*/
[class*=col-],
.col {
  width: 100%;
  max-width: 100%;
  padding-left: 12px;
  padding-right: 12px;
  margin: 0.4rem 0;
}

/*Grid Sistem*/
.col-ex {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: grid;
  place-items: center;
  border: 1px solid #c553a3 !important;
}

.bor {
  border: 1px solid #c553a3 !important;
}

